import styled from "@emotion/styled";

interface UnderliningProps {
    big: boolean;
    highlight: boolean;
}

const Underlining = styled.span<UnderliningProps>`
    box-shadow: inset 0 ${({ big }) => (big ? "-1rem" : "-.5rem")} 0
        ${({ highlight }) => (highlight ? "#2A2926" : "#252525")};
    transition: box-shadow 0.3s ease-out;
    &:hover {
        box-shadow: inset 0 ${({ big }) => (big ? "-2rem" : "-1rem")} 0
            "#2A2926";
    }
`;

export default Underlining;
