/* eslint-disable jsx-a11y/alt-text */
import React, { useState, useEffect, useRef } from "react";
import styled from "@emotion/styled";
import { motion, useAnimation } from "framer-motion";

import useOnScreen from "../hooks/useOnScreen";
import ContentWrapper from "../styles/ContentWrapper";
import Button from "../styles/Button";

const StyledSection = styled.section`
    width: 100%;
    height: auto;
    margin-top: 6rem;
`;

const StyledContentWrapper = styled(ContentWrapper)`
    && {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: 0;
        padding-left: 0;
        @media (min-width: 992px) {
            padding-right: 2.5rem;
            padding-left: 2.5rem;
        }
        .section-title {
            padding-right: 2.5rem;
            padding-left: 2.5rem;
            @media (min-width: 992px) {
                padding-right: 0;
                padding-left: 0;
            }
        }
    }
`;

const StyledInterests = styled.div`
    display: grid;
    /* Calculate how many columns are needed, depending on interests count */
    grid-template-columns: repeat(3, 15.625rem);
    grid-template-rows: repeat(2, auto);
    grid-auto-flow: column;
    column-gap: 1rem;
    row-gap: 1rem;
    padding: 0 2.5rem 0.25rem 2.5rem;
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar {
        display: none;
    }
    &::after {
        content: "";
        width: "17.125rem";
    }
    @media (min-width: 992px) {
        grid-auto-flow: row;
        grid-template-columns: repeat(3, 15.625rem);
        overflow: visible;
        padding: 0;
    }
    @media (hover: hover) {
        scrollbar-color: rgba(255, 255, 255, 0.5) transparent; // Firefox only
        &::-webkit-scrollbar {
            display: block;
            -webkit-appearance: none;
        }

        &::-webkit-scrollbar:horizontal {
            height: 0.8rem;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 8px;
            border: 0.2rem solid #121212;
            background-color: rgba(255, 255, 255, 0.5);
        }

        &::-webkit-scrollbar-track {
            background-color: #121212;
            border-radius: 8px;
        }
    }

    .interest {
        width: 13.625rem;
        height: 1rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 1rem;
        border: 0.125rem solid #fafafa;
        border-radius: 1rem;
        background: #1c1c1c;
        .icon {
            margin-right: 0.5rem;
        }
    }
`;

const interests = [
    {
        name: "Quick and Easy Setup",
        icon: "./icons/joystick.png",
    },
    {
        name: "Content via Markdown",
        icon: "./icons/notebook.png",
    },
    {
        name: "Responsive Design",
        icon: "./icons/nail-polish.png",
    },
    {
        name: "Medium Integration",
        icon: "./icons/medium.png",
    },
    {
        name: "Animations",
        icon: "./icons/gem-stone.png",
    },
    {
        name: "PWA (Offline Support)",
        icon: "./icons/mobile-phone.png",
    },
    {
        name: "Modern Tech-Stack",
        icon: "./icons/gatsby.png",
    },
    {
        name: "Blazing Fast",
        icon: "./icons/high-voltage.png",
    },
];

const Interests = () => {
    const [shownInterests, setShownInterests] = useState(5);

    const ref = useRef<HTMLDivElement | null>(null);
    const onScreen = useOnScreen(ref);

    const iControls = useAnimation();
    const bControls = useAnimation();

    useEffect(() => {
        const sequence = async () => {
            if (onScreen) {
                // i receives the value of the custom prop - can be used to stagger
                // the animation of each "interest" element
                await iControls.start((i) => ({
                    opacity: 1,
                    scaleY: 1,
                    transition: { delay: i * 0.1 },
                }));
                await bControls.start({ opacity: 1, scaleY: 1 });
            }
        };
        sequence();
    }, [onScreen, shownInterests, iControls, bControls]);

    const showMoreItems = () => setShownInterests(shownInterests + 4);

    return (
        <StyledSection id="interests">
            <StyledContentWrapper>
                <h3 className="section-title">title</h3>
                <StyledInterests ref={ref}>
                    {interests
                        .slice(0, shownInterests)
                        .map(({ name, icon }, key) => (
                            <motion.div
                                className="interest"
                                key={key}
                                custom={key}
                                initial={{ opacity: 0, scaleY: 0 }}
                                animate={iControls}
                            >
                                <img className="icon" /> {name}
                            </motion.div>
                        ))}
                    {shownInterests < interests.length && (
                        <motion.div
                            initial={{ opacity: 0, scaleY: 0 }}
                            animate={bControls}
                        >
                            <Button onClick={() => showMoreItems()}>
                                + Load more
                            </Button>
                        </motion.div>
                    )}
                </StyledInterests>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default Interests;
