import React, { useEffect } from "react";
import styled from "@emotion/styled";
import { motion, useAnimation } from "framer-motion";

import ContentWrapper from "../styles/ContentWrapper";
import Underlining from "../styles/Underlining";
import Social from "./Social";

const StyledSection = styled.section`
    width: 100%;
    height: auto;
    background: #121212;
`;

const StyledContentWrapper = styled(ContentWrapper)`
    && {
        width: 100%;
        height: 100%;
        min-height: 60vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 6rem;
        @media (min-width: 768px) {
            margin-bottom: 4rem;
        }
        .greetings {
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }
        .emoji {
            margin-left: 0.75rem;
            width: 2.2rem;
            height: 2.2rem;
            @media (min-width: 768px) {
                margin-left: 1rem;
                width: 3rem;
                height: 3rem;
            }
        }
        .title {
            margin-bottom: 1.5rem;
            @media (min-width: 768px) {
                margin-bottom: 0;
            }
        }
        .subtitle {
            margin-top: -0.75rem;
        }
        .description {
            font-size: 1.125rem;
            margin-bottom: 2rem;
        }
    }
`;

const AnimatedUnderlining = motion(Underlining);

const Introduction = () => {
    // Controls to orchestrate animations of greetings, emoji, social profiles, underlining
    const gControls = useAnimation();
    const eControls = useAnimation();
    const sControls = useAnimation();
    const uControls = useAnimation();

    // Start Animations after the splashScreen sequence is done
    useEffect(() => {
        const pageLoadSequence = async () => {
            eControls.start({
                rotate: [0, -10, 12, -10, 9, 0, 0, 0, 0, 0, 0],
                transition: { duration: 2.5, loop: 3, repeatDelay: 1 },
            });
            await gControls.start({
                opacity: 1,
                y: 0,
                transition: { delay: 0.4 },
            });
            await sControls.start({
                opacity: 1,
                x: 0,
            });
            // Animate underlining to hover state
            await uControls.start({
                boxShadow: `inset 0 -2rem 0 "#2A2926"
                    }`,
                transition: { delay: 0.4, ease: "circOut" },
            });
        };
        pageLoadSequence();
    }, [eControls, gControls, sControls, uControls]);

    return (
        <StyledSection>
            <StyledContentWrapper>
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={gControls}
                    data-testid="animated-heading"
                >
                    <h1 className="title">
                        <div className="greetings">
                            greetings
                            <motion.div
                                animate={eControls}
                                style={{ originX: 0.7, originY: 0.7 }}
                            ></motion.div>
                        </div>
                        title
                    </h1>
                    <h2 className="subtitle">
                        subtitlePrefix{" "}
                        <AnimatedUnderlining animate={uControls} big highlight>
                            subtitle
                        </AnimatedUnderlining>
                    </h2>
                    <div className="description"></div>
                </motion.div>
                <motion.div initial={{ opacity: 0, x: 20 }} animate={sControls}>
                    <Social />
                </motion.div>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default Introduction;
