import React, { ReactNode } from "react";
import styled from "@emotion/styled";

const StyledButton = styled.button`
    width: 15.625rem;
    height: 3rem;
    background-color: #fafafa;
    color: #121212;
    padding: 1rem;
    margin: 0;
    font-size: 0.875rem;
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    border-radius: 1rem;
    text-decoration: none;
    text-align: left;
    &:hover,
    &:focus,
    &:active {
        cursor: pointer;
        outline: none;
    }
    svg {
        height: 1rem;
        width: 1rem;
        margin-right: 0.3rem;
        margin-bottom: -0.175rem;
    }
`;

const Button = ({
    onClick,
    children,
}: {
    onClick: () => void;
    children: ReactNode;
}) => <StyledButton onClick={onClick}>{children}</StyledButton>;

export default Button;
