import React from "react";
import styled from "@emotion/styled";

import GithubIcon from "./icons/Github";

const StyledSocialWrapper = styled.div`
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: start;
    justify-items: start;

    margin-left: -2.5rem;
    margin-right: -2.5rem;
    padding-left: 2.5rem;
    padding-right: 2.5rem;

    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar {
        display: none;
    }
    @media (min-width: 1200px) {
        overflow: visible;
    }

    &::after {
        content: "";
        width: 2.5rem;
    }

    @media (hover: hover) {
        scrollbar-color: #121212 transparent; //Firefox only
        &::-webkit-scrollbar {
            display: block;
            -webkit-appearance: none;
        }

        &::-webkit-scrollbar:horizontal {
            height: 0.8rem;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 8px;
            border: 0.2rem solid #121212;
            background-color: #121212;
        }

        &::-webkit-scrollbar-track {
            background-color: #121212;
            border-radius: 8px;
        }
    }

    a {
        margin-right: 0.5rem;
        margin-bottom: 0.75rem;
        @media (min-width: 768px) {
            margin-right: 1rem;
        }
    }
`;

const StyledSocialProfile = styled.a`
    width: auto;
    height: auto;
    background: #121212;
    background: linear-gradient(to right, #fafafa 50%, #121212 50%);
    background-size: 205% 100%;
    background-position: right bottom;
    border-radius: 1rem;
    border: 0.125rem solid #fafafa;
    padding: 0.5rem 1.25rem;
    transition: all 0.1s ease-out;
    font-size: 1rem;
    font-weight: 500;
    color: #fafafa;
    line-height: 1rem;
    &:hover {
        background-position: left bottom;
        color: #121212;
    }
    &:hover svg {
        filter: invert(1);
    }
    svg {
        height: 1rem;
        width: 1rem;
        margin-right: 0.5rem;
        margin-bottom: -0.05rem;
    }
`;

const Social = () => {
    return (
        <StyledSocialWrapper>
            <StyledSocialProfile>
                <GithubIcon />
                Github
            </StyledSocialProfile>
        </StyledSocialWrapper>
    );
};

export default Social;
